<template>
  <Center>
      <form class=".login-container" @submit.prevent="handleSubmit()">
          <div class="form-item">
              <div class="input"><label>账号：</label><input v-model="userInfo.loginId" type="text" @input="validateEmpty('loginId','账号不能为空')" /></div>
              <FoemError :msg="error.loginId"/>
          </div>
          <div class="form-item">
              <div class="input"><label>密码：</label><input v-model="userInfo.loginPwd" type="password" @input="validateEmpty('loginPwd','密码不能为空')" /></div>
              <FoemError :msg="error.loginPwd"/>
          </div>
          <div class="form-item">
              <div class="input"><label>重复密码：</label><input v-model="userInfo.pwdAgein" type="password" @input="validatePwdAgain()" /></div>
              <FoemError :msg="error.pwdAgein"/>
          </div>
          <div class="form-item">
              <div class="input"><label>昵称：</label><input v-model="userInfo.nickname" type="text" @input="validateEmpty('nickname','请输入昵称')" /></div>
              <FoemError :msg="error.nickname"/>
          </div>
          <div class="form-item">
              <div class="input"><label></label><button>注册</button></div>
              <FoemError :isCenter="true" :msg="error.server"/>
          </div>
      </form>
  </Center>
</template>

<script>
import Center from '../components/Center'
import FoemError from '../components/FoemError'
import {reg} from '../services/userService'
export default {
    components:{
        Center,
        FoemError,
    },
    data(){
        return {
            //获取注册的信息
            userInfo:{
                //账号
                loginId:"",
                //密码
                loginPwd:"",
                //重复密码
                pwdAgein:"",
                //昵称
                nickname:"",
            },
            //错误消息
            error:{
                //账号
                loginId:"",
                //密码
                loginPwd:"",
                //重复密码
                pwdAgein:"",
                //昵称
                nickname:"",
                //服务器的错误消息
                server:"",
            }
        }
    },
    methods:{
        //非空验证
        validateEmpty(field,msg){
            //判断userInfo是否有值
            if(this.userInfo[field]){
                //如果有值错误消息为空
                this.error[field] = "";
                return true;
            }else {
                //如果没值传入指定错误消息
                this.error[field] = msg;;
                return false;
            }
        },
        //提交表单
        async handleSubmit(){
            //提交表单前，判断数据是否正确
            var validate1 = this.validateEmpty('loginId','账号不能为空');
            var validate2 = this.validateEmpty('loginPwd','密码不能为空');
            var validate3 = this.validatePwdAgain();
            var validate4 = this.validateEmpty('nickname','请输入昵称');
            if(validate1 & validate2 & validate3 & validate4){
                //提交数据
                var resp = await reg(this.userInfo);
                if(resp.data){
                    //注册成功
                    alert("注册成功！");
                    this.$router.push({
                        name:"Login",
                    });
                }else{
                    //注册失败
                    this.error.server = resp.msg;
                }
            }
        },
        //重复密码判断
        validatePwdAgain(){
            var result = this.validateEmpty("pwdAgein","请输入重复密码");
            if(result){
                if(this.userInfo.loginPwd !== this.userInfo.pwdAgein){
                    this.error.pwdAgein = "两次密码不一致";
                    return false;
                }
                return true;
            }
            return false;
        },
        
    },
}
</script>

<style scopde>
.login-container{
    width: 400px;
}

.form-item .input{
    display: flex;
}
.form-item label{
    width: 100px;
    display: inline-block;
    text-align: right;
    line-height: 41px;
}

.form-item input,
.form-item button{
    flex-grow: 1;
}
</style>